import React from 'react';
import { useState, useEffect, useCallback } from 'react';

export default function AddTargetModal(setIsAdding) {
  const [selfVal, setSelfVal] = useState(null);
  useEffect(() => {
    if (selfVal) {
      console.log('selfVal!', selfVal);
      const UIKit = require('uikit');
      UIKit.modal(selfVal).show();
    } else {
      console.log("no self val :(");
    }
  }, [selfVal]);
  const closeCallback = useCallback(ev => {
    ev.preventDefault();
    const UIKit = require('uikit');
    UIKit.modal(selfVal).hide();
    setIsAdding(false);
  }, []);
  const refCallback = useCallback(ref => {
    setSelfVal(ref);
  });
  return (
    <div uk-modal="true" ref={refCallback}>
      <div className="uk-modal-dialog">
        <button
          className="uk-modal-close-default"
          type="button"
          uk-close="true"
          onClick={closeCallback} />
        <div className="uk-modal-header">
          <h2 className="uk-modal-title">Modal Title</h2>
        </div>
        <div className="uk-modal-body">
          <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.</p>
        </div>
        <div className="uk-modal-footer uk-text-right">
          <button className="uk-button uk-button-default uk-modal-close" type="button">Cancel</button>
          <button className="uk-button uk-button-primary" type="button">Save</button>
        </div>
      </div>
    </div>
  )
}